@mixin heading-1-desktop {
  font-size: 50px;
  line-height: 54px;
  font-weight: 400;
}

@mixin heading-1-tablet {
  font-size: 40px;
  line-height: 44px;
}

@mixin heading-1-mobile {
  font-size: 30px;
  line-height: 34px;
}

@mixin  heading-2 {
  font-size: 24px;
  line-height: 29px;
  font-weight: 400;
}
